import { Component } from "react";

export default class PreviewOnsite extends Component{

    render(){
         const { title, subTitle,backgroundImage, selectedColor, btnText } = this.props;
        return (
            <div className="form-body">
                        <div className="uploader-preview">
                            <p className="text-center"><strong>Uploader Preview</strong></p>
                            <div className="onsite_preview">
                                <div className="onsite_preview_inner" style={{backgroundColor:selectedColor,backgroundImage:`url(${backgroundImage})`}}>
                                    <div className="onsite_submit_post_main">
                                        <div className="tb_onsite_post_inner">
                                            <div className="tb_onsite_upload_post">
                                                <div className="tb_onsite_box">
                                                    <div className="tb_onsite_box_header pt-4">
                                                        <div className="tb_onsite_logo_wrap"><img loading="lazy" src="https://cloud.taggbox.com/widget/themes/media/images/taggbox.svg" className="tb_onsite_popup_logo" width="400" height="50" alt="logo" /></div>
                                                    </div>
                                                    <div className="tb_onsite_box_title pt-0 pb-2">
                                                        <div className="tb_onsite_title">{title}</div>
                                                        <div className="tb_onsite_sub_heading mb-0">{subTitle}</div>
                                                    </div>
                                                    <div className="tb_onsite_box_body">
                                                        <div className="tb_onsite_post_tabs">
                                                            <div className="tb_onsite_separator">OR</div>
                                                            <div className="tb_onsite_tabs_nav">
                                                                <div className="tb_onsite_tabs active">Add Caption</div>
                                                                <div className="tb_onsite_tabs">Submit Review</div>
                                                            </div>
                                                        </div>
                                                        <div className="tb_onsite_tabs_content">
                                                            <div className="tb_onsite_add_caption_tab">
                                                                <div className="tb_onsite_image_upload_box mb-3">
                                                                    <div className="tb_onsite_upload_box_content">
                                                                        <div>
                                                                            <button type="button" className="btn">
                                                                                <input className="uppy-DragDrop-input" type="file" hidden="" name="files[]" accept="image/*,video/*" />
                                                                                <div className="uppy-DragDrop-inner">
                                                                                    <svg aria-hidden="true" focusable="false" className="uppy-c-icon uppy-DragDrop-arrow" width="16" height="16" viewBox="0 0 16 16">
                                                                                        <path d="M11 10V0H5v10H2l6 6 6-6h-3zm0 0" fillRule="evenodd"></path></svg>
                                                                                    <div className="uppy-DragDrop-label">Drag a video / image to upload (optional)</div>
                                                                                    <span className="uppy-DragDrop-note">Video Size : Max 100MB | Photo Size : Max 50MB | Max 3 Posts</span>
                                                                                </div>
                                                                            </button>
                                                                        </div>
                                                                        <div className="tb_onsite_uploader_status">
                                                                            <div>
                                                                                <div className="uppy-Root uppy-StatusBar is-waiting" aria-hidden="true" dir="ltr">

                                                                                    <div className="uppy-StatusBar-actions"></div>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div className="tb_onsite_add_caption_tab">
                                                                <div className="tb_onsite_form_group mb-2"><textarea className="tb_onsite_form_control_textarea" rows="2" placeholder="Add your caption here...."></textarea></div>
                                                                <div className="tb_onsite_form_actions">
                                                                    <div className="tb_onsite_btn_primary btn-sm">{btnText}</div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
        );
    }
}