import React, { Component } from 'react';
import $ from 'jquery';
import { Uppy } from "@uppy/core";
import Transloadit from "@uppy/transloadit";
import GoogleDrive from '@uppy/google-drive';
import Url from '@uppy/url';
import { DashboardModal } from '@uppy/react';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';

const uppyConfig = {
    uppyKey: '0701d3d7f8d94e92a65a9cba388b1d96',
    uppyBucket: 'cloud.taggbox.com',
    uppyAwsKey: '47ABVORKG52UYE1KB28S',
    uppyAwsSecret: 'pcaTfeokaz28geAAns08H3yaGbOVz6ILgTptu19v',
    companionUrl: 'https://test.taggbox.com'
}

export default class UploadImage extends Component {
    constructor(props) {
        super(props)
        this.uppy = new Uppy({ debug: true, autoProceed: false, restrictions: { maxFileSize: 5000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes: ['image/*'] } })
            .use(Transloadit, {
                target: '#a',
                params: {
                    auth: {
                        key: uppyConfig.uppyKey
                    },
                    steps: {
                        store: {
                            robot: "/wasabi/store",
                            bucket_region: "us-west-1",
                            bucket: uppyConfig.uppyBucket,
                            key: uppyConfig.uppyAwsKey,
                            secret: uppyConfig.uppyAwsSecret,
                            path: 'postimage/${file.id}'
                        }
                    }
                },
                waitForEncoding: true
            })
            .use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: uppyConfig.companionUrl })
            .use(Url, { target: Uppy.Dashboard, companionUrl: uppyConfig.companionUrl })
    }
    state = {
        backgroundModal: false
    }
    componentDidMount() {
        var self = this;
        this.uppy.on('transloadit:result', (stepName, result) => {
            self.props.onClickTouploadImage(`https://cloud.taggbox.com/postimage/${result.id}`, () => {
                setTimeout(() => {
                    this.uppy.cancelAll()
                    this.setState({ backgroundModal: false })
                }, 300)
            })
        })
    }
    onBackgroundClose = event => {
        this.uppy.cancelAll()
        this.setState({ backgroundModal: false })
    }
    onBackgroundOpen = event => {
        this.setState({ backgroundModal: true }, () => {
            $(".uppy-Dashboard-close").click(() => {
                this.uppy.cancelAll()
                this.setState({ backgroundModal: false })
            })
        })
    }
    render() {
        const { imageStyle, backgroundImage } = this.props
        const { backgroundModal } = this.state
        return <>
            <div className="d-flex align-items-start justify-content-between flex-column py-1">
                <label className="mb-1 fs-9 fw-medium">Background Image</label>
                {!backgroundImage ? <button type="button" className="btn btn-secondary btn-sm w-100 mb-2" onClick={()=>this.setState({backgroundModal:true})}>Attach Image</button>: null}
                {backgroundImage ? 
                <div className="card card-custom mb-2 w-100 rounded-1">
                    <div className="card-body d-flex flex-column p-1">
                        <div className="position-relative d-flex align-items-center cursor-pointer">
                            <div className="position-relative rounded-1 bg-light overflow-hidden w-100" style={imageStyle}>
                                <img className="img-fluid position-absolute w-100" loading="lazy" src={backgroundImage} height={500} width={300}  />
                            </div>
                            <div className="removeBanner position-absolute end-0 top-0 pt-2 pe-2">
                                <div className="d-flex align-items-center justify-content-end">
                                    <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Edit</Tooltip>}>
                                        <button type="button" className="btn btn-icon btn-secondary h-28px w-28px me-2" onClick={()=>this.setState({backgroundModal:true})}>
                                            <span className="svg-icon svg-icon-6 me-0">
                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                    <path fill="none" d="M0 0h24v24H0z" />
                                                    <path d="M16.757 3l-2 2H5v14h14V9.243l2-2V20a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V4a1 1 0 0 1 1-1h12.757zm3.728-.9L21.9 3.516l-9.192 9.192-1.412.003-.002-1.417L20.485 2.1z" fill="currentColor" />
                                                </svg>
                                            </span>
                                        </button>
                                    </OverlayTrigger>
                                    <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Remove</Tooltip>}>
                                        <button type="button" className="btn btn-icon btn-secondary h-28px w-28px" onClick={()=>this.props.onClickTouploadImage('',()=>{})}>
                                            <span className="svg-icon svg-icon-6 me-0">
                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                    <path fill="none" d="M0 0h24v24H0z" />
                                                    <path d="M7 4V2h10v2h5v2h-2v15a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V6H2V4h5zM6 6v14h12V6H6zm3 3h2v8H9V9zm4 0h2v8h-2V9z" fill="currentColor" />
                                                </svg>
                                            </span>
                                        </button>
                                    </OverlayTrigger>
                                </div>
                            </div>
                        </div>
                    </div>
                </div> : null}
            </div>
            {
                backgroundModal ? <DashboardModal
                    animateOpenClose={true}
                    open={true}
                    proudlyDisplayPoweredByUppy={false}
                    inline={false}
                    showProgressDetails={true}
                    height={750}
                    browserBackButtonClose={true}
                    showRemoveButtonAfterComplete={false}
                    uppy={this.uppy}
                    onRequestCloseModal={this.onBackgroundClose}
                    closeAfterFinish={true}
                    plugins={[]}
                /> : null
            }
        </>
    }

}
