import React, { Component, Suspense } from 'react';
import NeedSupport from '../../CommonComponents/NeedSupport';


class SubmitFeedRequest extends Component {

    render() {
        const { supportUrl, videoUrl, cancelFeed, sumbitFeed, loading, hideBtn } = this.props;
        return <div className="d-flex flex-wrap align-items-center justify-content-between">
            <Suspense fallback={<></>}>
                <NeedSupport content={'Need Help'} supportUrl={supportUrl} videoUrl={videoUrl} />
            </Suspense>
            <div className="my-1 d-flex align-items-center justify-content-end flex-wrap">
                <button type="button" className="btn btn-secondary me-3" onClick={() => cancelFeed()}>Cancel</button>
                {!hideBtn && <button type="button" className={`btn btn-primary ${loading ? 'spinner spinner-right disabled' : ''}`} onClick={() => sumbitFeed()}>Create Connections</button>}
            </div>
        </div>
    }
}

export default SubmitFeedRequest;