
import React, { Component, Suspense } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar'
import PreviewOnsite from './components/PreviewOnsite';
import UploadImage from './components/UploadImage';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { getOnsiteSettings, updateOnsiteFeed } from '../../../../Services/Actions/addFeedAction';
const ColorPicker = React.lazy(() => import('../../CommonComponents/ColorPicker'));

class OnsiteUpload extends Component {
    state = {
        onSiteEnable: false,
        loading: false,
        title: 'Submit your post',
        subTitle: 'You can share your reviews/rating here',
        error: false,
        subtitleError: false,
        selectedColor: '#eff2f3',
        btnText: 'Submit',
        btnTextError: false,
        msgError: false,
        backgroundImage: '',
        msg: 'The post(s) will be appeared after the admin approval',
    }

    componentWillMount() {
        getOnsiteSettings().then(response => {
            const { response_code, responseData } = response.data;

            if (response_code == 200) {
                const { feeds } = responseData;

                if (feeds && feeds.length > 0) {
                    const { onsite_settings } = feeds[0];
                    const settings = JSON.parse(onsite_settings);
                    if (settings && Object.keys(settings).length > 0) {
                        const { onsite_bg_color_wg, onsite_bg_image_wg, onsite_popup_btn_txt, onsite_popup_sub_heading, onsite_popup_title, onsite_succ_msg,onsite_status } = settings;
                        this.setState({ title: onsite_popup_title, subTitle: onsite_popup_sub_heading, btnText: onsite_popup_btn_txt, msg: onsite_succ_msg, backgroundImage: onsite_bg_image_wg, selectedColor: onsite_bg_color_wg,onSiteEnable: onsite_status==1?true:false })
                    }

                }
            }
        }).catch(ex => {
            console.log(ex);
        })
    }

    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, subtitleError: false, subtitleError: false, btnTextError: false, msgError: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }

    onColorChange = (value) => {
        this.setState({ selectedColor: value })
    }

    onClickTouploadImage = (value, callBack) => {
        this.setState({ backgroundImage: value }, () => callBack())
    }

    onSubmit = () => {
        const { title, subTitle, backgroundImage, selectedColor,onSiteEnable, btnText, msg } = this.state;

        if (title == '') {
            this.setState({ error: true });
        } else if (subTitle == '') {
            this.setState({ subTitle: true });
        } else if (btnText == '') {
            this.setState({ btnTextError: true });
        } else if (msg == '') {
            this.setState({ msg: true });
        } else {
            const obj = {
                network: 29,
                filter: 2,
                moderation: 0,
                onsite_qr_code_btn_text: "Scan QR code or click here to submit reviews/rating",
                onsite_btn_color: "#000000",
                onsite_btn_txt_color: "#ffffff",
                onsite_link_font_btn: "Lato",
                onsite_css_font_btn: "Lato",
                onsite_font_varient_btn: "",
                btn_fontSize: 16,
                onsite_qr_code_status: 1,
                onsite_popup_title: title,
                onsite_popup_btn_txt: btnText,
                onsite_popup_sub_heading: subTitle,
                onsite_term_url: "https://taggbox.com/on-site-uploads-content-rights/",
                onsiteImgBack: "",
                onsite_logo: "https://cloud.taggbox.com/uploaded/7a875377ce864fafa9f2fa65be28bb77",
                Onsite_logoImageValue: "",
                onsite_bg_color_wg: selectedColor,
                onsiteBackImage: "",
                onsite_bg_image_wg: backgroundImage,
                Onsite_backImageValue: "",
                onsite_succ_msg: msg,
                onsite_thank_txt_color: "#000000",
                onsite_link_font_msg: "Default",
                onsite_css_font_msg: "Default",
                onsite_font_varient_msg: "",
                onsite_thank_fontSize: 16,
                onsite_status: onSiteEnable?1:0
              };

            this.setState({ loading: true, error: false, subtitleError: false, subtitleError: false, btnTextError: false, msgError: false });
            updateOnsiteFeed(obj, () => { this.setState({ loading: false });
            this.props.history('/assets');
            this.props.editMediaHandleCallback(false);
        });
        }

    }
    render() {
        const imageStyle = {
            paddingBottom: '66%', width: '100%'
        };


        const { title, subTitle, error, onSiteEnable, subtitleError, backgroundImage, loading, selectedColor, btnText, msg, btnTextError, msgError } = this.state;
        return <> <div className="card card-custom card-body mb-3">
            <div className="d-flex align-items-start flex-column flex-md-row">
                <div className="uploadInnerAside flex-shrink-0 mb-3 mb-sm-0">
                    <label className="form-check form-switch py-2 align-items-center justify-content-between mb-2 h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 me-2">Enable</span>
                        <input className="form-check-input" type="checkbox" name="onSiteEnable" value={onSiteEnable} onChange={(e) => this.onCheckHandler(e)} />
                    </label>
                    <PerfectScrollbar>
                        <div className="upload_aside_scroll">
                            <div className="mb-2">
                                <label className="mb-1 fs-9 fw-medium">Title</label>
                                <div className="">
                                    <input type="text" name="title" className={`form-control form-control-sm ${error ? 'is-invalid' : ''}`} placeholder="Enter Sub Title..." value={title} onChange={(e) => this.onChangeHandler(e)} />
                                    {error ? <span className="invalid-feedback">This field is required</span> : null}
                                </div>
                            </div>
                            <div className="mb-2">
                                <label className="mb-1 fs-9 fw-medium">Sub Heading</label>
                                <div className="">
                                    <input type="text" name="subTitle" className={`form-control form-control-sm ${subtitleError ? 'is-invalid' : ''}`} placeholder="Enter Title..." value={subTitle} onChange={(e) => this.onChangeHandler(e)} />
                                    {subtitleError ? <span className="invalid-feedback">This field is required</span> : null}
                                </div>
                            </div>
                            <label className="d-flex align-items-center justify-content-between h-40px py-1">
                                <span className="ms-0 fs-9 fw-medium">Background Color</span>
                                <Suspense fallback={<></>}>
                                    <ColorPicker color={selectedColor} onChangeColor={this.onColorChange} />
                                </Suspense>
                            </label>
                            <UploadImage imageStyle={imageStyle} backgroundImage={backgroundImage} onClickTouploadImage={this.onClickTouploadImage} />
                            <div className="mb-2">
                                <label className="mb-1 fs-9 fw-medium">Submit Button Text</label>
                                <div className="">
                                    <input type="text" name="btnText" className={`form-control form-control-sm ${btnTextError ? 'is-invalid' : ''}`} placeholder="Enter Button Text..." value={btnText} onChange={(e) => this.onChangeHandler(e)} />
                                    {btnTextError ? <span className="invalid-feedback">This field is required</span> : null}
                                </div>
                            </div>
                            <div className="mb-2">
                                <label className="mb-1 fs-9 fw-medium">Thank You Message</label>
                                <div className="w-100 pb-2">
                                    <textarea className={`form-control textarea-min-row-5 fs-9 ${msgError ? 'is-invalid' : ''}`} name="msg" row={5} value={msg} onChange={(e) => this.onChangeHandler(e)}  >
                                    </textarea>
                                    {msgError ? <span className="invalid-feedback">This field is required</span> : null}
                                </div>
                            </div>
                        </div>
                    </PerfectScrollbar>
                </div>
                <div className="d-flex flex-grow-1 w-100 flex-column">
                    <PreviewOnsite {...this.state} />
                </div>
            </div>
        </div>
            <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
        </>
    }
}

export default OnsiteUpload;